html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
progress {
  vertical-align: baseline;
}
template,
[hidden] {
  display: none;
}
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
a:active,
a:hover {
  outline-width: 0;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
button,
input,
select,
textarea {
  font: inherit;
  margin: 0;
}
optgroup {
  font-weight: bold;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
html {
  font-size: 100%;
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  padding: 0;
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  line-height: 1.25;
  color: #000;
  background: #fff;
}
img {
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic;
  display: inline-block;
  vertical-align: middle;
}
.page-main {
  width: 1200px;
  padding: 0 20px;
  margin: auto;
}
.page-section {
  margin: 0 0 35px;
}
.section-header {
  background-color: #2199e8;
  color: #fff;
  margin-bottom: 15px;
  padding: 5px 20px;
}
.flex-col {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .flex-row {
    display: flex;
  }
  .flex-col {
    flex: 1 1 auto;
    margin: 0 1%;
    width: 48%;
  }
  .flex-col:first-child {
    margin-left: 0;
  }
  .flex-col:last-child {
    margin-right: 0;
  }
  .flex-col-1-3 {
    width: 31.33%;
  }
  .flex-col-2-3 {
    width: 64.66%;
  }
  .flex-col-1-4 {
    width: 23%;
  }
  .flex-col-3-4 {
    width: 73%;
  }
  .flex-col-equal {
    width: 50%;
  }
  .flex-col-main {
    width: 66.66%;
  }
  .flex-col-aside {
    width: 33.33%;
  }
}
@media (min-width: 1024px) {
  .flex-col-main {
    width: 75%;
  }
  .flex-col-aside {
    width: 25%;
  }
}
.flex-column {
  display: flex;
  flex-direction: column;
}
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
p {
  font-size: inherit;
  line-height: 1.6;
  margin-bottom: 1rem;
  text-rendering: optimizeLegibility;
}
em,
i {
  font-style: italic;
  line-height: inherit;
}
strong,
b {
  font-weight: 700;
  line-height: inherit;
}
small {
  font-size: 82%;
  line-height: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 400;
  color: inherit;
  text-rendering: optimizeLegibility;
  margin-top: 0;
  margin-bottom: 0.5rem;
  line-height: 1.4;
}
h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small {
  color: #cacaca;
  line-height: 0;
}
a {
  color: #2199e8;
  text-decoration: none;
  line-height: inherit;
  cursor: pointer;
  transition: color 0.3s;
}
a:hover,
a:focus {
  color: #0f5f94;
}
a img {
  border: 0;
}
hr {
  max-width: 1200px;
  height: 0;
  border-right: 0;
  border-top: 0;
  border-bottom: 1px solid #cacaca;
  border-left: 0;
  margin: 1rem auto;
  clear: both;
}
ul,
ol,
dl {
  line-height: 1.6;
  margin-bottom: 1rem;
  list-style-position: outside;
}
li {
  font-size: inherit;
}
ul {
  list-style-type: disc;
  margin-left: 1.25rem;
}
ol {
  margin-left: 1.25rem;
}
ul ul,
ul ol,
ol ul,
ol ol {
  margin-left: 1.25rem;
  margin-bottom: 0;
}
dl {
  margin-bottom: 1rem;
}
dl dt {
  margin-bottom: 0.3rem;
  font-weight: 700;
}
blockquote {
  margin: 0 0 1rem;
  padding: 0.5625rem 1.25rem 0 1.1875rem;
  border-left: 1px solid #cacaca;
}
blockquote,
blockquote p {
  line-height: 1.6;
  color: #8a8a8a;
}
cite {
  display: block;
  font-size: 0.82rem;
  color: #8a8a8a;
}
cite:before {
  content: '\2014 \0020';
}
abbr {
  color: #000;
  cursor: help;
  border-bottom: 1px dotted #000;
}
code {
  font-family: Consolas, 'Liberation Mono', Courier, monospace;
  font-weight: 400;
  color: #000;
  background-color: #e6e6e6;
  border: 1px solid #cacaca;
  padding: 0.125rem 0.3125rem 0.0625rem;
}
kbd {
  padding: 0.125rem 0.25rem 0;
  margin: 0;
  background-color: #e6e6e6;
  color: #000;
  font-family: Consolas, 'Liberation Mono', Courier, monospace;
}
.table-grid {
  border-collapse: separate;
  border-spacing: 2px;
  width: 100%;
}
.table-grid th {
  background-color: #2199e8;
  border-bottom: 7px solid #fff;
  color: #fff;
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
  padding: 7px 5px;
  text-transform: uppercase;
}
.table-grid td {
  font-size: 13px;
  line-height: 15px;
  padding: 10px 5px;
  text-align: center;
}
.table-grid tr:nth-child(odd) td {
  background-color: #e6e6e6;
}
.table-scroll {
  overflow-x: auto;
}
@media (max-width: 767px) {
  .responsive-table thead {
    display: none;
  }
  .responsive-table tr {
    display: block;
  }
  .responsive-table td {
    display: block;
    font-size: 16px;
    line-height: 1.25;
    padding: 5px 15px;
    text-align: left;
  }
  .responsive-table td:first-child {
    padding-top: 15px;
  }
  .responsive-table td:last-child {
    padding-bottom: 15px;
  }
  .responsive-table td:before {
    content: attr(data-th) ": ";
    font-weight: 700;
    margin-right: 10px;
  }
}
label {
  color: #2199e8;
  display: block;
  font-size: 14px;
  line-height: 1.79;
}
.form-field--inline > label {
  display: inline-block;
  vertical-align: middle;
  width: auto;
}
.form-control {
  border: 1px solid #d8d8d8;
  color: #000;
  font-size: 14px;
  line-height: 20px;
  padding: 5px 10px;
  width: 100%;
  height: 32px;
}
.form-control:focus {
  border-color: rgba(33, 153, 232, 0.5);
  outline: 1px solid rgba(33, 153, 232, 0.5);
}
.form-control::-moz-placeholder {
  color: #000;
  opacity: 1;
}
.form-control:-ms-input-placeholder {
  color: #000;
}
.form-control::-webkit-input-placeholder {
  color: #000;
}
.form-field {
  margin-bottom: 10px;
  position: relative;
}
.form-field--inline {
  align-items: center;
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
  white-space: nowrap;
}
.form-field--inline > * {
  flex: 1 1 auto;
  margin-left: 10px;
}
.form-group-flex-all {
  display: flex;
  margin-bottom: 10px;
}
.form-group-flex-all .form-field {
  flex: 1 1 0%;
  margin-bottom: 0;
}
.form-group-flex-all .form-field + .form-field {
  margin-left: 5px;
}
@media (min-width: 768px) {
  .form-field-inline-flex {
    align-items: center;
    display: inline-flex;
    margin-right: 25px;
    margin-bottom: 10px;
  }
  .form-field-inline-flex:last-child {
    margin-right: 0;
  }
  .form-field-inline-flex label {
    flex: 1 0 auto;
    padding-right: 10px;
  }
  .form-group-flex {
    display: flex;
    align-items: center;
    flex: 1 1 auto;
    margin-bottom: 10px;
  }
  .form-group-flex > .form-group {
    flex: 1 1 auto;
  }
  .form-group-flex .form-field {
    flex: 1 1 0%;
    margin-bottom: 0;
  }
  .form-group-flex .form-field--button {
    flex: 0 0 auto;
  }
  .form-group-flex .form-field + .form-field {
    margin-left: 5px;
  }
}
.form-group-inline .form-field {
  display: inline-block;
  vertical-align: top;
  margin-right: 10px;
}
.form-group-inline .form-field:last-child {
  margin-right: 0;
}
.form-group-inline .form-field .form-control,
.form-group-inline .form-field .ui-selectmenu-button {
  width: 240px !important;
  max-width: 100%;
}
button:focus {
  outline: none;
}
.btn {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  line-height: 1.25;
  padding: 0;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
}
.btn-primary {
  background-color: #2199e8;
  border-radius: 5px;
  color: #fff;
  padding: 10px 20px;
  text-transform: uppercase;
}
.btn-primary:hover,
.btn-primary:focus {
  background-color: #777;
  color: #fff;
}
.btn-attention {
  background-color: #ffae00;
  border-radius: 5px;
  color: #2199e8;
  padding: 10px 20px;
  text-transform: uppercase;
}
.btn-attention:hover,
.btn-attention:focus {
  background-color: #d78400;
  color: #fff;
}
.btn-large {
  font-size: 21px;
  min-width: 150px;
  max-width: 100%;
  text-align: center;
}
.btn-small {
  font-size: 12px;
  line-height: 1.67;
  padding: 6px 12px;
}
.btn-link {
  display: inline;
  padding: 0;
}
.btn-link:hover,
.btn-link:focus {
  border-bottom: 1px solid;
}
